@import "mixins";
@import "keyframes";

@include keyframes-dijalog-pulse;

.dijalog.dijalog-theme-wireframe {
  padding-top: 160px;
  padding-bottom: 160px;

  .dijalog-overlay {
    background: rgba(255, 255, 255, 0.4);
  }

  .dijalog-content {
    font-family: "Helvetica Neue", sans-serif;
    background: white;
    color: black;
    border: 2px solid black;
    padding: 2em;
    position: relative;
    margin: 0 auto;
    max-width: 100%;
    width: 400px;
    font-size: 1.1em;
    line-height: 1.5em;

    h1,
    h2,
    h3,
    h4,
    h5,
    h6,
    p,
    ul,
    li {
      color: inherit;
    }
  }

  .dijalog-close {
    position: absolute;
    top: 0;
    right: 0;
    cursor: pointer;

    &:before {
      position: absolute;
      content: "\00D7";
      font-size: 40px;
      font-weight: normal;
      line-height: 80px;
      height: 80px;
      width: 80px;
      text-align: center;
      top: 3px;
      right: 3px;
      color: black;
    }

    &:hover:before,
    &:active:before {
      color: black;
    }
  }

  .dijalog-form {
    .dijalog-message {
      margin-bottom: 0.5em;
    }

    .dijalog-input {
      margin-bottom: 1em;

      textarea,
      input[type="date"],
      input[type="datetime"],
      input[type="datetime-local"],
      input[type="email"],
      input[type="month"],
      input[type="number"],
      input[type="password"],
      input[type="search"],
      input[type="tel"],
      input[type="text"],
      input[type="time"],
      input[type="url"],
      input[type="week"] {
        background: white;
        width: 100%;
        padding: 0.25em 0.67em;
        font-family: inherit;
        font-weight: inherit;
        font-size: inherit;
        min-height: 2.5em;
        margin: 0 0 0.25em;
        border: 2px solid black;

        &:focus {
          border-style: dashed;
          outline: none;
        }
      }
    }

    .dijalog-buttons {
      @include dijalog-clearfix;
    }
  }

  .dijalog-button {
    border-radius: 0;
    border: 0;
    float: right;
    margin: 0 0 0 0.5em;
    font-family: inherit;
    text-transform: uppercase;
    letter-spacing: 0.1em;
    font-size: 0.8em;
    line-height: 1em;
    padding: 0.75em 2em;

    &.dijalog-last {
      margin-left: 0;
    }

    &:focus {
      animation: dijalog-pulse 1.1s infinite;
      outline: none;
    }

    &.dijalog-button-primary {
      background: black;
      color: white;
      border: 2px solid transparent;
    }

    &.dijalog-button-secondary {
      background: white;
      color: black;
      border: 2px solid black;
    }
  }
}
